.header
{
  position: sticky;
  .coedlib{
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: fixed;
    top: 10px;
    margin-bottom: 16px;
    color: rgb(47, 189, 189);
  }
}
.main-theme{
  position: relative;
  background: gray;
.theme{
  position: absolute;
  right: 0;

  .img{
    padding: 10px;
    transition: transform 0.4s ease;
  }
  .img:hover{
    cursor: pointer;
    transform: scale(1.4);
  }
}
}
.search-container{
  .form
  {
    margin-top: 10px;
    padding:2px;
    text-align: center;
    #search-input{
      padding: 8px;
      border: 1px solid #ccc;
      border-radius: 4px;
      margin-right: 8px;
      font-size: 16px;
    }
    #search-input:focus {
      outline: none;
      border-color: #007bff;
    }
    #search-input::placeholder {
      color: #999;
    }
    #search-button {
      padding: 8px 16px;
      background-color: #007bff;
      color: #fff;
      border: none;
      border-radius: 4px;
      font-size: 16px;
      cursor: pointer;
    }
    #search-button:hover {
      background-color: #0056b3;
    }
  }
}
.header-part{
 position: sticky;
 top: 0;
 background-color: rgb(37, 37, 37);
 height: auto;
}
.book-card {
  /*display:inline-block;
  text-align: center;
  display: inline-block;*/

  text-align: center;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 20px;

  padding: 20px;
  .book-info{
    padding: 10px;
    .book-info1{
      padding: 5px;
    }
  }
  #books-ul{
    list-style-type:inherit;
  }
}
.home{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.3em;
  height:auto;
  display: block;
  text-align: center;
  padding: 10px;
  word-spacing: 10px;
}
.menu{
  background: grey;
  .menu-ul{
    top: 0;
    list-style-type:  none;
    display: inline-flex;
    #li{
      padding: 5px;
      transition: transform 1s  ease;
    }
    #li:hover{
      cursor: pointer;
      transform: scale(1.2);
    }
  }
}
.book-img{
  width: 200;
}
.dark{
  background: black;
  color: aqua;
  width: 100%;
  height: 100%;
  .preview{
    color: white;
  }
  }
.light{
    background: #fff;
    color: brown;
    width: 100%;
    height: 100%;
    .preview{
      color:black;
    }
    .contact-form form{
      background-color: #fff;
    }
}
#main{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
}
/* Hide the scroll bar */
::-webkit-scrollbar {
  display: none;
}
.loader{
  color: blue;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.animated-paragraph {
  animation-name: fadeIn;
  animation-duration: 1s;
  animation-fill-mode: backwards;
}
.animated-paragraph.animate {
  animation-name: fadeIn;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}
.paragraph-full-content {
  display: none;
}
.read-more-button {
  background-color: aquamarine;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  transition: transform 1s  ease;
}
.read-more-button:hover{
  transform: scale(1.2);
  color: gray;
}
#featured{
  display: block;
  text-align: center;
}
.contact-form{
  width: 500px;
  max-width: 100%;
  margin: auto;
  
}
.contact-form h2{
  text-align: center;
}
.contact-form form {
  text-align: center;
  width: 300px;
  margin: 0 auto;
  padding: 20px;
  background-color: black;
  border-radius: 5px;
  font-family: Arial, sans-serif;
}

.contact-form  label {
  display: block;
  margin-bottom: 10px;
  font-weight: bold;
}

input[type="text"],
.contact-form input[type="email"] {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.contact-form input[type="submit"] {
  background-color: #4CAF50;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
}

.contact-form input[type="submit"]:hover {
  background-color: #45a049;
}
@media only screen and (max-width:750px){
  .book-img{
    width: 150px;
    padding: 20px;
  }
  .preview  p{
    font-size: 0.8em;
  }
  .download button{
    padding: 8px 16px;
    background-color: #0d67c7;
    color: #fff;
    border: none;
    border-radius: 5px;
    font-size: 10px;
    cursor: pointer;
  }
  .homie{
    max-width: 100%;
    
  }
  .homie  img{
    width: 427px;
  }
  .main-homie  p{
    font-size: 1.2em;
  }
  .footer{
    font-size: x-small;
  }
}
.preview{
  font-size: 1.3em;
}
.download{
  padding: 30px;
}
.download-btn{
  padding: 8px 16px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
}
.download-btn:hover{
  background: green;
}
.homie{
  display: grid;
  justify-content: center;
  align-items: center;
  max-width: 100%;
  overflow: hidden;
}
.homie-p{
  font-size: 2em;
  text-align: center;
  font-style: oblique;
}
        /* Define the animation keyframes */
   @keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
  }

  /* Apply the animation to the text element */
  .homie {
    animation: fadeIn 2s ease-in-out;
  }
  .homie-p {
    animation: scaleText 2s ease-in-out;
  }

  @keyframes scaleText {
    0% { transform: scale(1); }
    50% { transform: scale(1.2); }
    100% { transform: scale(1); }
  }

.footer{
  text-align: center;
  color: grey;
  font-size: small;
}
.contact-form form{
  background-image: url('homie.png');
  background-repeat: no-repeat;
  background-size: cover;
}
